.glassmorphism-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  border-radius: 12px;
  overflow: hidden;
}

.glassmorphism-title {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}


.card-container {
  transition: filter 0.3s ease;
}

.card-wrapper:hover {
  transform: scale(1.1);
  transition: transform 0.3s ease;
}

.card-image {
  width: 100%;
  height: auto;
  transition: transform 0.5s ease;
  cursor: pointer;
}

.card-image:hover {
  transform: scale(1.1);
}

.image-container {
  width: 100%;
  overflow: hidden;
  border-radius: 10px;
  margin-bottom: 10px;
}