/* App.css */

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
}

@keyframes shimmer {
  0% {
    background-position: 200%;
  }
  100% {
    background-position: -200%;
  }
}

.gradient-container {
  position: relative;
  overflow: hidden;
  background: linear-gradient(250deg,#7fa4e9,#7C6BD1);
  background-size: 200% 200%;
}